<template>
    <div id="myTeam" :style="{backgroundImage:'url('+bg+')'}">
        <div class="searchTop">

          <router-link to="/register">
            <div class="searchTopLeft">
              <img src="./../assets/images/game92.png" alt="" class="ico">
              <p>注册</p>
            </div>
          </router-link>
          <div class="searchTopRight">
            <input type="text" placeholder="搜索用户ID">
          </div>
        </div>
        <div class="teamBox">
          <h4 class="teamBoxTop">我的团队（3人）</h4>
          <div class="teamList">
            <div v-for="(teamList,index) in teamLists" :key="index" class="divList">
                <div class="teamUser">
                   <div class="teamUserImg"><img :src="teamList.img" alt=""></div>
                   <div class="teamUserIfo">
                      <span class="teamUserName">{{teamList.teamName}}</span>
                      <p  class="teamUserId">id：{{teamList.teamId}}</p>
                      <p class="teamUserState">状态：<span>{{teamList.teamUserState}}</span></p>
                   </div>
                </div>
                <div class="teamUserCenter" @click="toFollow">
                  <img src="./../assets/images/game94.png" alt="">
                  <p>上下分</p>
                </div>
                <div class="teamUserRight">
                    <p>余额 ：{{teamList.teamBalance}}</p>
                    <p>上分 ：{{teamList.teamshangF}}</p>
                </div>
            </div>
          </div>
        </div>
      <!-- 底部导航菜单 -->
      <tabs :selected="selected"></tabs>

      <Popup v-model="popupVisible" position="bottom">
        <div class="popup-container">
          <div class="bottomNr">
            <span class="closeBtn" @click="closeBtn"><img src="./../assets/images/game97.png" alt=""></span>
            <img src="./../assets/images/game96.png" alt="">上分，下分
          </div>
          <div class="userInfo">
             <div class="userInfoImg"><img src="./../assets/images/game4.png" alt=""></div>
             <div class="userInfoName">
                <p class="userInfoNameTxt">曹刚先生</p>
                <p class="userInfoNamePop">当前鱼店：45</p>
             </div>
             <div class="userInfoBtn">上下分记录</div>
          </div>
          <ul class="tabTilte">
            <li @click="activeTabCont=0" :class="{active:activeTabCont==0,'leftList':true}">上分</li>
            <li @click="activeTabCont=1" :class="{active:activeTabCont==1,'centerList':true}">下分</li>
          </ul>
          <div class="tabCenter">
             <div v-show="activeTabCont==0">
               <input type="text" placeholder="请输入上分值">
             </div>
             <div v-show="activeTabCont==1">
               <input type="text" placeholder="请输入上分值">
             </div>
          </div>
          <div class="btnConfirm">确定操作</div>
          <p class="textBottom">请添加客服转账后，发起上下分申请</p>

        </div>
      </Popup>
    </div>
</template>

<script>
import tabs from './../components/tabs'
import { Popup } from 'mint-ui'
export default {
  name: 'myTeam',
  data () {
    return {
      selected: '', // 导航当前页的状态
      popupVisible: false,
      bg: require('./../assets/images/game21.png'), // 背景图片
      teamLists: [
        {
          img: require('./../assets/images/game4.png'), // 头像
          teamName: '曹刚', // 用户名
          teamId: 255, // 用户ID
          teamUserState: '正常', // 用户状态
          teamBalance: 435, // 用户余额
          teamshangF: 100 // 用户上分
        },
        {
          img: require('./../assets/images/game4.png'), // 头像
          teamName: '阿达', // 用户名
          teamId: 266, // 用户ID
          teamUserState: '异常', // 用户状态
          teamBalance: 67, // 用户余额
          teamshangF: 34 // 用户上分
        }
      ],
      activeTabCont: 0
    }
  },
  components: {
    tabs, Popup
  },
  methods: {
    toFollow () {
      this.popupVisible = true
    },
    closeBtn () {
      this.popupVisible = false
    }
  }
}
</script>

<style scoped>
  #myTeam{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

  /*头部搜索样式*/
  .searchTop{display: inline-block; width: 100%;margin-top: 1rem;}
  .searchTopLeft{float: left; width: 20%; float: left; text-align: center; color: #fff;}
  .searchTopLeft .ico{width: 1.2rem;}
  .searchTopLeft p{font-size: 0.8rem;color: #fff;}
  .searchTopRight{float: left; width: 70%;}
  .searchTopRight input{border: 1px solid #cd2e48; color: #fff; background: url("./../assets/images/game93.png") no-repeat 0.5rem 0.6rem; outline: none; background-size:7%;height: 2.5rem;width: 100%;border-radius: 20px;text-indent: 2.5rem;}
  .searchTopRight input::-webkit-input-placeholder{color:#6c2026;}
  .searchTopRight input::-moz-placeholder{color:#6c2026;}
  .searchTopRight input:-moz-placeholder{color:#6c2026;}
  .searchTopRight input:-ms-input-placeholder{color:#6c2026;}

  /*团队样式*/

  .teamBox{display: inline-block; width: 90%; margin-left: 5%; margin-top: 1.5rem;}
  .teamBoxTop{display: inline-block; height: 2rem; font-size: 0.8rem; text-indent: 0.4rem; font-weight: 700; line-height: 2rem; color: #fff; width: 100%; background: url("./../assets/images/game95.png") no-repeat; background-size: 100% 100%;}
  .teamList{display: inline-block; width: 99.5%; border: 1px solid #cd2e48; border-top: none;}
  .teamList .divList{display: inline-block; width: 100%;padding: 1rem 0;}
  .teamList .divList:nth-child(even){background: rgba(58, 23, 18, 0.81);}
  .teamList .divList .teamUser{float: left;}

  .teamBox{display: inline-block; width: 100%;margin-top: 1.5rem;}
  .teamBoxTop{display: inline-block; height: 2rem; font-size: 0.8rem; text-indent: 0.8rem; font-weight: 700; line-height: 2rem; color: #fff; width: 100%; background: url("./../assets/images/game95.png") no-repeat; background-size: 100% 100%;}
  .teamList{display: inline-block; width: 99.5%; border-top: none;}
  .teamList li{display: inline-block; width: 100%;padding: 0.6rem 0; border-bottom: 1px solid #cd2e48;}
  .teamList li .teamUser{float: left;}

  .teamUser .teamUserImg{float: left;margin-left:0.8rem;}
  .teamUser .teamUserImg img{width: 3.5rem; border-radius: 100px;}
  .teamUser .teamUserIfo{float: left;margin-left: 0.8rem;}
  .teamUserIfo .teamUserName{color: #ffdd68; font-size: 0.9rem;display:inline-block;margin-bottom: 0.2rem;}
  .teamUserIfo .teamUserId{color: #c1c1c1; font-size: 0.7rem;}
  .teamUserIfo .teamUserState{color: #c1c1c1; font-size: 0.7rem;}
  .teamUserIfo .teamUserState span{color: #fff;}
  .teamUserCenter{float: left; width: 40%; vertical-align: middle; text-align: center;color: #ffdd68; font-size: 0.8rem;}
  .teamUserCenter img{width: 1.2rem; padding-bottom: 0.3rem;margin-top: 0.5rem;}
  .teamUserRight{float: right; color: #c1c1c1; font-size: 0.8rem;padding-top: 0.8rem; text-align: right; padding-right: 0.8rem;}

  /*底部弹框样式*/
  .popup-container{display:block;width: 100%;background: #250e0b;}
  .bottomNr{border-bottom: 1px solid #707070; font-size: 0.8rem; color: #fff;padding-top: 1rem;padding-left: 1rem;padding-bottom: 0.8rem;}
  .bottomNr img{width: 1.2rem;vertical-align: middle;margin:-0.3rem 0.6rem 0 0;}
  .userInfo{display: inline-block; width: 100%;margin-top: 0.6rem;border-bottom: 1px solid #707070;padding-bottom: 0.4rem;}
  .userInfoImg{float: left;padding-left: 1rem;margin-right: 0.8rem;}
  .userInfoImg img{width: 3.5rem;border-radius: 100px;}
  .userInfoName{display: inline-block; vertical-align: middle;margin-top: 0.6rem;}
  .userInfoNameTxt{color: #ffdd68;font-size: 0.9rem;margin-bottom: 0.4rem;}
  .userInfoNamePop{color: #c1c1c1;font-size: 0.9rem; }
  .userInfoBtn{float: right; color: #a0a0a0; border: 1px solid #a0a0a0; padding: 0.4rem 0.6rem; border-radius: 6px;    border-radius: 6px;vertical-align: middle;margin: 0.7rem 1.5rem 0 0;}
  .tabTilte{display: block; width: 70%;margin: 1rem auto 0 auto;}
  .tabTilte li{display: inline-block; width: 46%; padding: 0.6rem 0; border-radius: 5px; background: #81211f; color: #a0a0a0; font-size: 0.7rem; text-align: center; }
  .tabTilte li:nth-child(2){margin-left: 2%;}
  .tabTilte .active{background: #a02221; color: #fff;}
  .tabCenter{display: block; width: 70%;margin: 1rem auto 0 auto;}
  .tabCenter input::-webkit-input-placeholder{color:#949292;}
  .tabCenter input::-moz-placeholder{color:#949292;}
  .tabCenter input:-moz-placeholder{color:#949292;}
  .tabCenter input:-ms-input-placeholder{color:#949292;}
  .tabCenter input{border: 1px solid #949292; outline: none; color: #949292; text-align: right; font-size: 0.8rem;padding-right: 0.4rem; background: none; height: 1.6rem; line-height: 1.6rem; width: 92%; border-radius: 5px;}
  .btnConfirm{width: 66%; border-radius: 5px; background: #a02221; color: #fff; font-size: 1rem; padding: 0.6rem 0; text-align: center; margin: 1rem auto 0 15%;}
  .textBottom{color: #a0a0a0; font-size: 0.8rem; padding-left: 15%; margin-top: 1rem;padding-bottom: 1rem;}
  .closeBtn{float: right;}



















</style>



